<template>
  <div class="order">
      <!-- table -->
      <navbar></navbar>

      <orderTable :data = "data" v-on:reloadPage="getReloadPage" ></orderTable>

      <div class="footer">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="pageSize"
          layout="total, prev, pager, next"
          :total="total">
        </el-pagination>
      </div>

  </div>
</template>

<script>
import axios from 'axios'
import $ from 'jquery'
import url from '../url'
import navbar from './navbar'
import orderTable from './orderTable'
export default {
  name: 'order',
  data () {
    return {
      data: [],
      orderUrl: url.orderUrl,
      deleteTripUrl: url.deleteTripUrl,
      pageSize: '',
      total: '',

      currentPage1: 5,
    }
  },
  created() {
    let _this = this
    this.getUserId()
    this.getOrder('0')
    .then(e => {
      console.log(e)
      for(let i of e.message.list) {
        i.travel_date = this.fmtDate(i.travel_date)
        i.end_date = this.fmtDate(i.end_date)
      }
      _this.data = e.message.list
      _this.total = e.message.total
      _this.pageSize = e.message.pageSize
    })
  },
  methods: {
    
    fmtDate(obj){
      let _this = this
      const date =  new Date(obj);
      const y = 1900+date.getYear();
      const m = "0"+(date.getMonth()+1);
      const d = "0"+date.getDate();
      return y+"-"+m.substring(m.length-2,m.length)+"-"+d.substring(d.length-2,d.length);
    },
    getReloadPage(data) {
      this.data = data.message.list
      this.total = data.message.total
      this.pageSize = data.message.pageSize
    },
    handleCurrentChange(val) {
      let _this = this
      this.getOrder(val)
      .then(e => {
        console.log(e)
        _this.data = e.message.list
      })
    },
    getUserId() {
      let userId = this.$route.params.id
    },
    getOrder(page) {
      let url = this.orderUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"GET",
            url: url+page,
            crossDomain: true,
            xhrFields:{withCredentials:true},
            // contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    }
  },
  components: {
    navbar,
    orderTable,

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-pagination{
  text-align: center;
}
.cell{
  text-align: center;
}
</style>



