<template>
  <div class="login_box">
    <el-form ref="form" :model="user" label-width="80px">


      <el-form-item label="账号" label-width="50px">
        <el-input v-model="user.username"></el-input>
      </el-form-item>

      <el-form-item label="密码" label-width="50px">
        <el-input v-model="user.password" type="password"></el-input>
      </el-form-item>


      <el-form-item style="text-align:center; margin-left: -80px;">
        <el-button type="default" @click="goSignIn">注册</el-button>
        <el-button type="primary" @click="$loginInThen">登录</el-button>
      </el-form-item>


    </el-form>
  </div>
</template>

<script>
import url from '../url.js'
import axios from 'axios'
import $ from 'jquery'
export default {
  name: 'login_box',
  data () {
    return {
      user: {
          username: '',
          password: '',
      },
      // loginIn url
      loginUrl: url.loginUrl,
    }
  },
  methods: {
    $loginInThen() {
      this.$loginIn().then(e => {
        console.log(e)
        if(e.code === 200) {
          this.$router.push({
            name: 'order',
            params: {
              id: e.message
            }
          })
        }
        else{
          this.$message.error(e.message)
        }
      })
    },
    $loginIn() {
      let user_name = this.user.username
      let password = this.user.password
      let url = this.loginUrl
      let user = {
        user_name,
        password
      }
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url,
            data: JSON.stringify(user),
            crossDomain: true,
            xhrFields:{withCredentials:true},
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    loginIn() {
      let user_name = this.user.username
      let password = this.user.password
      console.log(this.password)
      axios.post(this.loginUrl, {
        user_name,
        password
      })
      .then(e => {
        console.log(e)
        if(e.data.code === 200) {
          this.$router.push({
            name: 'order',
            params: {
              id: e.data.message
            }
          })
        }
        else{
          this.$message.error(e.data.message)
        }
      })
    },
    goSignIn() {
      this.$emit('goSignIn', true)
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login_box  {
  border:1px solid #909399;
  margin:auto;
  border-radius: 5px;
  padding:30px;
  background: white;
  width:40%;
  height:25%;
  margin: auto;
  position: absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;

}
</style>



