<template>
  <div class="createCard">
    <el-card>
      <!-- header -->

      <div slot="header" class="clearfix">
        <span>行程</span>
        <!-- <el-button type="primary" style="margin-left:30px;" @click="createNewDay">添加天数</el-button> -->
      </div>
      
      <!-- body -->
      
      <el-card v-for="(i, key2) of data" :key="key2">
        <!-- dayNumber and title -->
          <el-tag>DAY{{i.day}}</el-tag>
          <el-button type="primary"
                     size="small"
                     style="margin-left: 10px;"
                     @click="createDetail(key2)">
                     新增描述
          </el-button>
          <el-input v-model="i.title" placeholder="请输入当天标题" style="width:300px; margin-left:10px;"></el-input>
          <!-- <el-button type="danger"
                     size="small"
                     style="float: right"
                     @click="deteleDay(key2)">
                     删除行程
          </el-button> -->
          <i class="el-icon-delete"   style="float: right; margin-left:20px; cursor:pointer" @click="deteleDay(key2)"></i>
          <i class="el-icon-circle-plus"   style="float: right; cursor:pointer" @click="insertDay(i)"></i>
          <el-card>
              <!-- detail -->

              <div v-for="(j, key) of i.itemDescriptions" style="margin-top:20px;">

                <el-input
                  type="textarea"
                  autosize
                  placeholder="请输入内容"
                  style="width:80%"
                  v-model="i.itemDescriptions[key]">
                </el-input>
                <!--
                <el-button type="danger"
                           size="small"
                           style="margin-left: 10px;"
                           @click="deleteDetail(key2, key)">
                           删除
                </el-button>
               -->

                <i class="el-icon-delete deleteIcon"   @click="deleteDetail(key2, key)"></i>

              </div>
              </el-input>
          </el-card>
      </el-card>

      <el-button type="primary" style="float:right;margin-bottom:10px;" @click="saveCard">保存</el-button>

    </el-card>

  </div>
</template>

<script>

export default {
  name: 'createCard',
  data () {
    return {
      data: [{
        day: 1,
        title:'',
        itemDescriptions: ['']
      }],

    }
  },
  created() {

  },
  methods: {
    saveCard() {
      let data = this.data
      this.$emit('getCard', data)
    },
    createNewDay() {
      let data = this.data
      let len = data.length+1
      data.push({
        day:len,
        title: '',
        itemDescriptions: ['']
      })
    },
    createDetail(key) {
      let data = this.data
      data[key].itemDescriptions.push('')
    },
    deleteDetail(key2, key) {
      let data = this.data
      this.$confirm('您要删除此描述吗？', '提示', {
         confirmButtonText: '确定',
         cancelButtonText: '取消',
         type: 'warning'
       }).then(() => {

         data[key2].itemDescriptions.splice(key, 1)

         this.$message({
           type: 'success',
           message: '删除成功!'
         });
       }).catch(() => {
         this.$message({
           type: 'info',
           message: '已取消删除'
         });
       });


    },
    deteleDay(key2) {
      let data = this.data

      this.$confirm('您要删除当天行程吗？', '提示', {
         confirmButtonText: '确定',
         cancelButtonText: '取消',
         type: 'warning'
       }).then(() => {

         data.splice(key2, 1)

         for(let i =0; i<data.length; i++) {
           data[i].day = i+1
         }

         this.$message({
           type: 'success',
           message: '删除成功!'
         });
       }).catch(() => {
         this.$message({
           type: 'info',
           message: '已取消删除'
         });
       });
    },
    insertDay(items) {
      let data = this.data
      let day = items.day
      this.data.splice(day, 0, {
        day,
        title: '',
        itemDescriptions: ['']
      })

      for(let i =0; i<data.length; i++) {
        data[i].day = i+1
      }
    },
    switchData(){
      if(this.oldData.length) return this.oldData
      return this.data
    }
  },
  components: {
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-card{
  margin: 10px 0;
}
.settingMessage{
  width:80%;
  height:80%;
  z-index:100;
}
.deleteIcon{
  line-height: 33px;
  cursor:pointer;
}
</style>



