<template>
  <div class="create">
    <!-- navbar -->
    <navbar></navbar>
    <!-- plane  new setting-->
    <plane v-on:getPlane="getPlaneData" v-show="nextPage === 0"></plane>
    <!-- main -->
    <!-- <car v-show="nextPage === 1"></car> -->
    <createCard v-on:getCard="dadGetCard" v-show="nextPage === 1"></createCard>
    
    <!-- setting message -->
    <settingMessage v-on:getMess="dadGetMess"></settingMessage>
    
  </div>
</template>

<script>
import navbar from '../order/navbar'
import plane from './plane'

import settingMessage from './settingMessage'
import axios from 'axios'
import createCard from './createCard'
import url from '../url.js'
import $ from 'jquery'
export default {
  name: 'create',
  data () {
    return {
      trip: {},
      plane: [],
      url: url.saveUrl,
      nextPage: 0
    }
  },
  created() {

  },
  methods: {
    getPlaneData(data) {
      this.plane = data
      this.nextPage = 1
    },
    test() {
      let url = this.url
      let trip = this.trip
      trip.plane = this.plane
      console.log(trip)
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url,
            data: JSON.stringify(trip),
            crossDomain: true,
            xhrFields:{withCredentials:true},
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {

              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    getUserId() {
      let userId = this.$route.params.id
    },
    dadGetMess(data) {
      this.trip = data
    },
    dadGetCard(data) {
      this.trip.tripItems = data
      let items = this.trip.tripItems

      for(let i of items) {
        let item = i.itemDescriptions
        for(let j=0; j<item.length; j++) {
          item[j] = {
            description: item[j],
            sort_id: j
          }
        }
      }
      this.test().then(e => {
        console.log(e)
        if(e.code === 1) {
          this.$message.error(e.message)
        }
        else{
          let message = e.message
          this.$message({
            message,
            type: 'success'
          })
          this.$router.push({
            name: 'order',
            params: {
              id: 1
            }
          })

        }


      })
    }

  },
  components: {
    navbar,
    createCard,
    settingMessage,
    plane,
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



