<template>
  <div class="sign_box">
    <el-form ref="form" :model="user" >
      <p class="form_title">注册页面</p>


      <el-form-item label="姓名" label-width="100px">
        <el-input v-model="user.name" @focus="alert('姓名', '请输入真实姓名。')"></el-input>
      </el-form-item>


      <el-form-item label="用户名" label-width="100px">
        <el-input v-model="user.username" @focus="alert('账号', '请输入您的账号。')"></el-input>
      </el-form-item>



      <el-form-item label="密码" label-width="100px">
        <el-input v-model="user.password1" type="password" @focus="alert('密码','请输入您的密码，3-15位包含英文数字即可。')"></el-input>
      </el-form-item>

      <el-form-item label="二次输入密码" label-width="100px">
        <el-input v-model="user.password2" type="password" @focus="alert('二次输入','请再次输入您的密码。')"></el-input>
      </el-form-item>

      <el-form-item label="部门" label-width="100px">
        <el-radio-group v-model="user.department">
          <el-radio-button label="泰国部"></el-radio-button>
          <el-radio-button label="日本部"></el-radio-button>
          <el-radio-button label="新马部"></el-radio-button>
        </el-radio-group>
      </el-form-item>


      <el-form-item style="text-align:center; ">
        <el-button type="primary" @click="goLoginIn">注册</el-button>
      </el-form-item>


    </el-form>
  </div>
</template>

<script>
import url from '../url.js'
import axios from 'axios'
export default {
  name: 'sign_box',
  data () {
    return {
      user: {
          username: '',
          name: '',
          password1: '',
          password2: '',
          department: '',
      },
      signUrl: url.signUrl
    }
  },
  methods: {
    goLoginIn() {
      let user = this.user
      let ps1 = user.password1
      let ps2 = user.password2
      let name = user.name
      let username = user.username
      let department = user.department
      let signUrl = this.signUrl
      let reg = /^[0-9a-zA-Z]+$/

      if(!name.length) {
        this.$message.error('名字不能为空。')
        return
      }

      if(!username.length) {
        this.$message.error('用户名不能为空')
        return
      }

      if( !(ps1 == ps2) ) {
        this.$message.error('两次输入密码不相同，请重新填写。')
        ps1 = ''
        ps2 = ''
        return
      }

      if((ps1.length < 3) || (ps1.length > 15) ) {
        this.$message.error('请输入3-15位的密码并包含英文数字，请重新填写。')
        ps1 = ''
        ps2 = ''
        return
      }

      if(!department.length) {
        this.$message.error('请选择您负责的区域。')
      }

      if(department == '新马部') department = 1
      if(department == '泰国部') department = 2
      if(department == '日本部') department = 3

      axios.post(signUrl, {
        user_name: username,
        nickname: name,
        department_id: department,
        password: ps1
      })
      .then(e => {
        console.log(e)
        if(e.data.code === 1 ) {
          this.$message.error(e.data.message)
          for(let i in this.user) {
            user[i] = ''
          }
        }
        else{
          this.$message(e.data.message)
          this.$emit('goLoginIn', true)

          for(let i in this.user) {
            user[i] = ''
          }
        }

      })

    },
    alert(title, message) {
      this.$notify({title, message})
    },
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.sign_box  {
  border:1px solid #909399;
  margin:auto;
  border-radius: 5px;
  padding:30px;
  background: white;
  width:40%;
  height:60%;
  margin: auto;
  position: absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
}
.form_title{
  text-align: center;
  margin:10px 0;
}
</style>



