<template>
  <div class="login">
    <!--main page -->
    <!-- login_box -->
    <login_box v-on:goSignIn= "goSignIn" v-show="isOpenLogin"></login_box>
    <!-- sign_box -->
    <sign_box v-on:goLoginIn= "goLoginIn" v-show="isOpenSign"></sign_box>
  </div>
</template>

<script>
import login_box from './login_box'
import sign_box from './sign_box'


export default {
  name: 'login',
  data () {
    return {
      // switch box args
      isOpenSign: false,
      isOpenLogin: true,

      
    }
  },
  methods: {
    // switch function
    goSignIn(data) {
      this.isOpenSign = data
      this.isOpenLogin = !data
    },
    goLoginIn(data) {
      this.isOpenSign = !data
      this.isOpenLogin = data
    },
  },
  components: {
    login_box,
    sign_box
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{
  height:100%;
  background-image: url(https://drscdn.500px.org/photo/121224777/h%3D1065/v2?webp=true&sig=0e32c5563963cc5fe23584866e96e6ee49890f77876eb8094382beb15ac4b638);
  background-repeat:no-repeat;
  background-size:100% 100%;-moz-background-size:100% 100%;
}
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.el-col {
  border-radius: 4px;
}

.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}

</style>



