<template>
  <div class="settingMessage">
    <el-dialog title="行程单信息"
               :visible.sync="dialogFormVisible"
               center
               :show-close="false"
               :close-on-click-modal="false">

       <el-tabs v-model="activeName">
         <el-tab-pane label="填写行程单" name="first">
           <el-form :model="mess">
             <el-form-item label="行程单标题" :label-width="formLabelWidth">
               <el-input v-model="mess.title" auto-complete="off"></el-input>
             </el-form-item>
             <el-form-item label="订单号" :label-width="formLabelWidth">
               <el-input v-model="mess.itemNumber" auto-complete="off"></el-input>
             </el-form-item>
             <el-form-item label="出行人姓名" :label-width="formLabelWidth">
               <el-input v-model="mess.customer_name" auto-complete="off"></el-input>
             </el-form-item>
             <el-form-item label="出行人昵称" :label-width="formLabelWidth">
               <el-input v-model="mess.customer_wechat" auto-complete="off" laceholder="请填写准确微信昵称(非微信账号)。"></el-input>
             </el-form-item>
             <el-form-item label="出行人手机号" :label-width="formLabelWidth">
               <el-input v-model="mess.phoneNumber" auto-complete="off"></el-input>
             </el-form-item>
             <el-form-item label="出发地" :label-width="formLabelWidth">
               <el-input v-model="mess.start_point" auto-complete="off"></el-input>
             </el-form-item>
             <el-form-item label="目的地" :label-width="formLabelWidth">
               <el-input v-model="mess.end_point" auto-complete="off"></el-input>
             </el-form-item>
             <el-form-item label="出行日期" :label-width="formLabelWidth">
               <el-date-picker
                 v-model="mess.travel_date"
                 value-format="yyyy-MM-dd"
                 type="date"
                 placeholder="选择日期">
               </el-date-picker>
             </el-form-item>
             <el-form-item label="归团日期" :label-width="formLabelWidth">
               <el-date-picker
                 v-model="mess.end_date"
                 value-format="yyyy-MM-dd"
                 type="date"
                 placeholder="选择日期">
               </el-date-picker>
             </el-form-item>
           </el-form>
          <div class="button_box">
             <el-button type="primary" @click="saveMessage">保存</el-button>
             <el-button type="danger" @click="backOrder">返回</el-button>
          </div>
         </el-tab-pane>
         <el-tab-pane label="上传行程单" name="second">

           <iframe id="iframe_display" name="iframe_display" style="display: none"></iframe>
           <!-- fake form  -->
           <form :action="uploadUrl" method="post" enctype="multipart/form-data" target="iframe_display">
              <input type="text" v-model="messUpload.title" v-show="false"  name="title"/>
              <input type="text" v-model="messUpload.itemNumber" v-show="false"  name="itemNumber"/>
              <input type="text" v-model="messUpload.customer_name" v-show="false" name="customer_name" />
              <input type="text" v-model="messUpload.customer_wechat"  v-show="false" name="customer_wechat"/>
              <input type="text" v-model="messUpload.travel_date"  v-show="false" name="travel_date"/>
              <input type="text" v-model="messUpload.end_date"  v-show="false" name="end_date" />
              <input type="text" v-model="messUpload.start_point"  v-show="false" name="start_point" />
              <input type="text" v-model="messUpload.end_point"  v-show="false" name="end_point" />
              <input type="text" v-model="messUpload.phoneNumber"  v-show="false" name="phoneNumber" />

              <el-form :model="messUpload">
                <el-form-item label="行程单标题" :label-width="formLabelWidth">
                  <el-input v-model="messUpload.title" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="订单号" :label-width="formLabelWidth">
                  <el-input v-model="messUpload.itemNumber" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="出行人姓名" :label-width="formLabelWidth">
                  <el-input v-model="messUpload.customer_name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="出行人昵称" :label-width="formLabelWidth">
                  <el-input v-model="messUpload.customer_wechat" auto-complete="off" placeholder="请填写准确微信昵称(非微信账号)。"></el-input>
                </el-form-item>
                <el-form-item label="出行人手机号" :label-width="formLabelWidth">
                  <el-input v-model="messUpload.phoneNumber" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="出发地" :label-width="formLabelWidth">
                  <el-input v-model="messUpload.start_point" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="目的地" :label-width="formLabelWidth">
                  <el-input v-model="messUpload.end_point" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="出行日期" :label-width="formLabelWidth">
                  <input type="date" v-model="messUpload.travel_date"  name="travel_date"/>
                </el-form-item>
                <el-form-item label="归团日期" :label-width="formLabelWidth">
                  <input type="date" v-model="messUpload.end_date"   name="end_date" />
                </el-form-item>


              </el-form>
                <input type="file" name="file"  />

               <input type="submit" value="upload" id="postSubmit" v-show="false"/>
            </form>
            <div class="button_box">
              <el-button type="primary" v-loading.fullscreen.lock="fullscreenLoading" @click="saveMessageUpload">上传</el-button>
              <el-button type="danger" @click="backOrder">返回</el-button>
            </div>

         </el-tab-pane>
       </el-tabs>

    </el-dialog>


  </div>
</template>

<script>
import navbar from '../order/navbar'
import axios from 'axios'
import url from '../url.js'
import $ from 'jquery'
export default {
  name: 'settingMessage',
  data () {
    return {
      formLabelWidth: '100px',
      dialogFormVisible: true,
      showClose: "false",
      activeName: 'second',
      fullscreenLoading: false,

      mess: {
        title: '',
        itemNumber: '',
        customer_name: '',
        customer_wechat: '',
        travel_date: '',
        end_date: '',
        start_point: '',
        end_point: '',
        phoneNumber: '',

      },

      messUpload: {
        title: '',
        itemNumber: '',
        customer_name: '',
        customer_wechat: '',
        travel_date: '',
        end_date: '',
        start_point: '',
        end_point: '',
        phoneNumber: '',
      },
      uploadUrl: url.uploadUrl
    }
  },
  created() {
  },
  methods: {
    backOrder() {
      this.$router.push({
        name: 'order',
        query: {
          id: 1
        }
      })
    },
    getUserId() {
      let userId = this.$route.params.id
    },
    saveMessageUpload() {
      let _this =this
      let upload = this.messUpload
      for(let i in upload) {
        if(!upload[i].length){
          _this.$message.error('信息不能为空！')
          return
        }
      }
      document.getElementById("postSubmit").click()
      this.fullscreenLoading = true
      setTimeout(function() {
        _this.fullscreenLoading = false
        _this.$message({
          type: 'success',
          message: '已成功上传！'
        })
        _this.$router.push({
          name: 'order',
          query: {
            id: 1
          }
        })
      }, 3000)


    },
    saveMessage() {
      let mess = this.mess

      for(let i in mess) {
        if(!mess[i].length) {
          this.$message.error('信息不能为空！')
          return
        }
      }


      this.dialogFormVisible = false
      this.$emit('getMess', mess)
    }
  },
  components: {
    navbar,
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.button_box{
  text-align: center;
}
</style>



