import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login/login'
import Order from '@/components/order/order'
import Create from '@/components/create/create'
import OrderDetail from '@/components/order_detail/order_detail'
import OrderPlane from '@/components/order/plane_comp'
import TravelItinerary from '@/components/order/travel_itinerary'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
      path: '/order',
      name: 'order',
      component: Order,
    },
    {
      path: '/create',
      name: 'create',
      component: Create
    },
    {
      path: '/order_detail',
      name: 'order_detail',
      component: OrderDetail
    },
    {
      path: '/order_plane',
      name: 'plane_comp',
      component: OrderPlane
    },
    {
      path: '/travel_itinerary',
      name: 'travel_itinerary',
      component: TravelItinerary
    }

  ]
})



